<template>
  <div style="max-width: 100%;" class="needBalanceDetail">
    <myHeader
      :isBack="true"
      backPath="/needBalanceRecord"
      title="结算记录">
    </myHeader>
    <div class="seat-head"></div>
    <div class="main">
      <mt-cell title="批次号：" :value="balanceData.batchName"></mt-cell>
      <mt-cell title="结算日期:" :value="balanceData.bizdate"></mt-cell>
      <mt-cell title="投苗均重/kg:" :value="balanceData.avgWeight"></mt-cell>
      <mt-cell title="上市均重/kg:" :value="balanceData.listedverWeight"></mt-cell>
      <mt-cell title="正品率:" :value="balanceData.realrate"></mt-cell>
      <mt-cell title="料肉比:" :value="balanceData.meatrate"></mt-cell>
    </div>
    <div class="main-bottom">
      <div class="category jus-b ali-c" @click="isShow">
        <div class="left">结算明细:</div>
        <div class="img"></div>
      </div>
      <!-- 结算明细 -->
      <section class="ett-info" v-show="showlist">
        <div class="jus-b ali-c">
          <div class="ett-name">猪苗金额</div>
          <div>{{balanceData.balanceDetail.pigamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">饲料金额</div>
          <div>{{balanceData.balanceDetail.feedsettamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">兽药金额</div>
          <div>{{balanceData.balanceDetail.veterdurgsettamt}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">回收金额</div>
          <div>{{balanceData.balanceDetail.recycleamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">扣罚金额</div>
          <div>{{balanceData.balanceDetail.lrbamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">补贴金额</div>
          <div>{{balanceData.balanceDetail.subsidies}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">结算金额</div>
          <div>{{balanceData.balanceDetail.settamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">补缴保证金</div>
          <div>{{balanceData.balanceDetail.payment}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">扣代付款</div>
          <div>{{balanceData.balanceDetail.payforcustamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">应付金额</div>
          <div>{{balanceData.balanceDetail.payableamount}}</div>
        </div>
      </section>
    </div>
    
    <Signature v-show="isShowSignature" ref="signature"></Signature>
    
    <SubmitBtn
      v-if="recordState != 2 && billStatusIndex==30"
      @click.native="submitData()">
    </SubmitBtn>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import Signature from '@/components/Signature'
  import {balanceDeatils, saveBalanceRecordsigner} from '@/api/materialApply'
  import SubmitBtn from "@/components/SubmitBtn";
  import {getParam} from '@/utils/wxUtil'
  import {Indicator} from 'mint-ui'
  
  export default {
    name: 'needBalanceDetail',
    data() {
      return {
        showlist: true,
        balanceData: {
          batchName: '',
          batchId: '',
          applyDate: '',
          avgKg: '',
          bizdate: '',
          bthcnt: '',
          cfsscps: '',
          cfsszps: '',
          chl: '',
          cpjz: '',
          lrb: '',
          shjz: '',
          ssrl: '',
          zpjz: '',
          balanceDetail: {
            pigamount: '',
            feedsettamount: '',
            veterdurgsettamt: '',
            recycleamount: '',
            lrbamount: '',
            subsidies: '',
            settamount: '',
            payment: '',
            payforcustamount: '',
            payableamount: ''
          }
        },
        rcordId: '',
        recordState: '',
        billStatusIndex: '',
        signerImgs: '',
        isShowSignature: true
      }
    },
    components: {
      SubmitBtn,
      myHeader,
      Signature
    },
    created() {
      this._getBalanceRecord();
      // this.rcordId = this.$route.params.rcordId;
      this.rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    },
    watch: {
      recordState(newName, oldName) {
        // 已完成签名，只需要展示签名图片，不能编辑
        if (this.recordState == 2 && this.billStatusIndex == 30) {
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.disabled = true;
            this.$refs.signature.fromDataURL(this.signerImgs);
          });
        }
    
        if (this.billStatusIndex == 30 && this.recordState != 2) { // 已经审核，可以签字，可以编辑
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.disabled = false;
          });
        }
    
        if (this.billStatusIndex != 30) {  // 没有审核或者已经取消
          this.isShowSignature = false;
        }
    
      }
    },
    methods: {
      _getBalanceRecord() {
        // const rcordId = this.$route.params.rcordId;
        const rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
        balanceDeatils(rcordId).then(res => {
          Indicator.close();
          let data = res.data;
          data.state ? this.recordState = data.state : null;
          data.billStatusIndex ? this.billStatusIndex = data.billStatusIndex : null;
          data.signerUrl.length ? this.signerImgs = data.signerUrl[0].img : null;
          
          this.balanceData = {
            batchName: data.batchName,
            batchId: data.batchId,
            bizdate: data.bizdate,
            avgWeight: data.avgWeight,
            listedverWeight: data.listedverWeight,
            realrate: data.realrate,
            meatrate: data.meatrate,
            balanceDetail: {
              pigamount: data.balanceDetail.pigamount,
              feedsettamount: data.balanceDetail.feedsettamount,
              veterdurgsettamt: data.balanceDetail.veterdurgsettamt,
              recycleamount: data.balanceDetail.recycleamount,
              lrbamount: data.balanceDetail.lrbamount,
              subsidies: data.balanceDetail.subsidies,
              settamount: data.balanceDetail.settamount,
              payment: data.balanceDetail.payment,
              payforcustamount: data.balanceDetail.payforcustamount,
              payableamount: data.balanceDetail.payableamount
            }
          }
        }).catch(err => {
          Indicator.close();
        });
      },
      isShow() {
        this.showlist = !this.showlist;
      },
      submitData() {
        let signerImg = this.$refs.signature.save();
        
        const data = {
          rcordId: this.rcordId,
          fileEntry: {
            imgContent: signerImg,
            imgType: 'png'
          }
        };
        
        saveBalanceRecordsigner(data).then(res => {
          this.$toast({
            message: '签字确认成功！',
            duration: 1000
          });
          this.$router.push('/needBalanceRecord');
        });
        
      }
    }
  }
</script>

<style lang="scss">
  .needBalanceDetail {
    min-height: 100vh;
    background-color: #F8F8F8;
    
    .main {
      margin-top: .12rem;
    }
    
    .main-bottom {
      background-color: #fff;
      
      .category {
        padding: 0 .14rem;
        height: .45rem;
        font-size: .16rem;
        border-bottom: 1px solid #ddd;
        
        .img {
          width: .12rem;
          height: .08rem;
          background: url('~@/assets/icon-next.png') no-repeat;
          background-size: 100%;
        }
      }
      
      // 结算明细
      .ett-info {
        padding: .17rem .15rem 0 .3rem;
        
        .jus-b {
          height: .3rem;
          font-size: .14rem;
          color: rgba(136, 136, 136, 1);
        }
      }
    }
  }


</style>



